<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 电影订单
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-select v-model.number="query.userType" placeholder="请选择">
                    <el-option v-for="item in UserTypeAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-date-picker class="handle-input-min"
                        v-model="query.beginDay"
                        type="date" value-format="yyyy-MM-dd"
                        placeholder="选择开始日期">
                </el-date-picker>
                <el-date-picker class="handle-input-min"
                        v-model="query.endDay"
                        type="date" value-format="yyyy-MM-dd"
                        placeholder="选择结束日期">
                </el-date-picker>
                <el-input v-model="query.filmName" placeholder="电影名称" class="handle-input mr10"></el-input>
                <el-input v-model="query.userName" placeholder="用户名" class="handle-input mr10"></el-input>

                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
                    :summary-method="getSummaries"
                    show-summary
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="UserName" label="用户名" width="100"></el-table-column>
                <el-table-column prop="FullName" label="姓名"></el-table-column>
                <el-table-column label="用户类别">
                    <template slot-scope="scope">{{scope.row.UserType|UserTypeName}}</template>
                </el-table-column>
                <el-table-column prop="FilmName" label="电影名称"></el-table-column>
                <el-table-column prop="SettlementModesName" label="结算方式"></el-table-column>
                <el-table-column label="级别">
                    <template slot-scope="scope">{{scope.row.BuyLevel|MemberLevelName}}</template>
                </el-table-column>
                <el-table-column label="状态">
                    <template slot-scope="scope">{{scope.row.State|FilmOrderStateName}}</template>
                </el-table-column>
                <el-table-column label="投资金额">
                    <template slot-scope="scope">{{scope.row.BuyGold|money}}</template>
                </el-table-column>
                <el-table-column prop="DailyRate" label="日化率" width="150">
                    <template slot-scope="scope">{{scope.row.DailyRate*100|money}}%+{{scope.row.AddDailyRate*100|money4}}%</template>
                </el-table-column>

                <el-table-column label="收益金额">
                    <template slot-scope="scope">{{scope.row.CurSumRebateGold|money}}</template>
                </el-table-column>
                <el-table-column label="收益次数">
                    <template slot-scope="scope">{{scope.row.CurCycleCount}}</template>
                </el-table-column>
                <el-table-column label="预估的分红">
                    <template slot-scope="scope">{{scope.row.EstimateRebateGold|money}}</template>
                </el-table-column>

                <el-table-column prop="CreatedAt" label="购买时间"></el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <el-dialog title="裁剪图片" :visible.sync="dialogVisible" width="30%">
            <vue-cropper ref='cropper' :src="imgSrc" :ready="cropImage" :zoom="cropImage" :cropmove="cropImage" style="width:100%;height:300px;"></vue-cropper>
            <span slot="footer" class="dialog-footer">
                    <el-button @click="cancelCrop">取 消</el-button>
                    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
                </span>
        </el-dialog>

    </div>
</template>
<script>
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';

    export default {
        name: 'FilmOrderList',
        data() {
            return {
                defaultSrc: require('../../assets/img/img.jpg'),
                fileList: [],
                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
                FilmStateAll:[
                    {name:'全部',value:0},
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                FilmStates:[
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                BuyLevelTypeAll:[
                    {name:'全部',value:0},
                    {name:'新手会员',value:1},
                    {name:'白银会员',value:2},
                    {name:'黄金会员',value:3},
                    {name:'钻石会员',value:4},
                    {name:'皇冠会员',value:5}
                ],
                BuyLevelTypes:[
                    {name:'新手会员',value:1},
                    {name:'白银会员',value:2},
                    {name:'黄金会员',value:3},
                    {name:'钻石会员',value:4},
                    {name:'皇冠会员',value:5}
                ],
                UserTypeAll:[
                    {name:'全部',value:0},
                    {name:'一般玩家',value:1},
                    {name:'业务玩家',value:2},
                ],
                query: {
                    userType: 0,
                    beginDay:'',
                    endDay:'',
                    buyLevel: 2,
                    filmName: '',
                    userName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                tableData: [],
                GroupData:{},
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.getData();
        },
        components: {
            VueCropper
        },
        methods: {
            getSummaries(){
                const sums = [];
                sums[1] = "合计："+ this.GroupData.C+"条";
                sums[8] = this.GroupData.BuyGold;
                sums[10] = this.GroupData.ArriveRebateGold.toFixed(2);
                sums[12] = this.GroupData.EstimateRebateGold.toFixed(2);

                return sums;
            },

            // 获取 easy-mock 的模拟数据
            getData() {
                request({ url:'filmbll/getfilmorderlist', method: 'post',data: this.query }).then((res)=>{
                    if (res.code==200){
                        console.log(res.obj.ListData);
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                        this.GroupData = res.obj.GroupData;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 200px;
        display: inline-block;
    }
    .handle-input-min {
        width: 140px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
</style>
